import classNames from 'classnames';
import React from 'react';
import { CampaignStatus } from '../../../types/campaign';

interface CampaignStatusBadgeProps {
    status: CampaignStatus;
    className?: string;
}

export const CampaignStatusBadge: React.FC<CampaignStatusBadgeProps> = ({ status, className }) => {
    const getStatusClasses = () => {
        switch (status) {
            case 'completed':
                return 'bg-green-100 text-green-800 border-green-200';
            case 'sending':
                return 'bg-blue-100 text-blue-800 border-blue-200';
            case 'scheduled':
                return 'bg-purple-100 text-purple-800 border-purple-200';
            case 'paused':
                return 'bg-yellow-100 text-yellow-800 border-yellow-200';
            case 'failed':
                return 'bg-red-100 text-red-800 border-red-200';
            case 'draft':
                return 'bg-gray-100 text-gray-800 border-gray-200';
            default:
                return 'bg-gray-100 text-gray-800 border-gray-200';
        }
    };

    return (
        <span className={classNames('inline-flex px-2 py-1 text-xs font-semibold rounded border uppercase', getStatusClasses(), className)}>
            {status}
        </span>
    );
};
